
@import url('https://fonts.googleapis.com/css?family=Creepster');
.halloweenPlaylist {
  background-image: url('../img/ghost.gif');
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}
#back-home {
  color: darkred;
  font-size: 25px;
  font-family: 'Creepster', cursive;
}
button {
  background-color: orange;
  color: black;
  padding: 30px 40px;
  border-radius: 3px;
  margin-top: 300px;
  margin-bottom: 100px;
  border: none;
  display: block;
  font-size: 25px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Creepster', cursive;
}




