
.dogGenerator {
  text-align: center;
}
.headers-div {
  	display: block;
  	margin-left: auto;
  	margin-right: auto;
  	margin-top: 50px;
  	background: #85e085;
  	width: 400px;
  	padding: 5px;
  	border-radius: 10px;
    font-size: 42;
    margin-bottom: 30px;
}
.subheader {
  	display: block;
  	margin-left: auto;
  	margin-right: auto;
  	background: #80bfff;
  	padding: 20px;
  	width: 400px;
  	border-radius: 10px;
    margin-bottom: 30px;
}
.gif-button {
  	background-color: #cd6eef;
  	color: white;
  	font-size: 20px;
  	border: none;
  	padding: 20px;
  	border-radius: 10px;
  	margin: left;
  	margin-bottom: 20px;
}
.gif-button:hover {
    cursor: pointer;
}



